.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(rgba(0, 0, 0, 0.945) ,rgba(0, 0, 255, 0.959));
}

.tittle{
    font-size: 60px;
    color: white;
    animation: uptittle 2s;
}

@keyframes uptittle {

    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);
    }
    
}

.input{
    background-color: slategray;
    padding: 15px;
    margin: 34px 0;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 2px 7px brgba(0, 0, 0, 0.733)
}

.input > input{
    background-color: transparent;
    outline: none;
    border: 0px;
    font-size: 20px;
    margin-right: 8px;
    color: white;
}

.input > input::placeholder{
    color: aliceblue;
}

.button{
    background-color: transparent;
    border: 0px;
    color: white;
    cursor: pointer;
}

.button:hover{
    scale: 1.4;
}

.main{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column; 
   width: 400px;
   background-color: aliceblue;
   border-radius: 10px;
}


.main > h3{
    font-size: 30px;
    margin: 12px 0;
}

.main > span{
    font-size: 20px;
    margin-bottom: 15px;
    font-weight: bold;
}